.experience-container {
    margin: 4rem 0;
    position: relative;
    margin-left: 4rem;
}
.experience-container h5 {
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 3rem;
}

.arrow-left,
.arrow-right {
    width: 2.2rem;
    height: 2.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #6751b9;
    border-radius: 0.65rem;
    border:1.5px solid #6751b9;
    background: #130f2a;
    position: absolute;
    top: 55%;
    z-index: 2;
    cursor: pointer;
}
.arrow-left ChevronLeftOutlinedIcon,
.arrow-rigth ChevronRightOutlinedIcon {
    font-size: 2rem;
    font-weight: 500;

}

.arrow-left {
    left: -2rem;
}
.arrow-right {
    right: -2rem;
}
@media (max-width:2560px){
    .arrow-left{
        left: -1rem;
    }
    .arrow-right{
        right: 3rem;
    }
}
@media (max-width:1440px){
    .arrow-left{
        left: -1rem;
    }
    .arrow-right{
        right: 3rem;
    }
}
@media (max-width : 760px){
    .experience-container {
        font-size: 1.3rem;
        margin-bottom: 2rem;
    }
}
@media (max-width : 425px){
    .experience-container {
        font-size: 1.3rem;
        margin-bottom: 2rem;
    }
    .arrow-left{
        left: 0rem;
    }
    .arrow-right{
        right: 0rem;
    }
}
@media (max-width : 375px){
    .experience-container {
        font-size: 1.3rem;
        margin-bottom: 2rem;
    }
    .arrow-left{
        left: -1rem;
    }
    .arrow-right{
        right: -1rem;
    }
}
@media (max-width : 320px){
    .experience-container {
        font-size: 1.3rem;
        margin-bottom: 2rem;
    }
    .arrow-left{
        left: -1rem;
    }
    .arrow-right{
        right:-1rem;
    }
}
